'use strict';

var _rotor = require('./core/rotor');

var _renderer = require('./core/renderer');

var _vue = require('./components/vue.app');

var root = new _rotor.Rotor(75, 0, 9);

// : DEBUG


var secondaryRotor = new _rotor.Rotor(100, 0, 3);
secondaryRotor.addPen(100, 0);

root.addRotorLink(secondaryRotor, 0);

var tertiaryRotor = new _rotor.Rotor(175, 0, 6);
tertiaryRotor.addPen(175, 0, 'red');

secondaryRotor.addRotorLink(tertiaryRotor, 0);

document.addEventListener('DOMContentLoaded', function () {
  // :: get DOM hooks
  var cyclorama = document.getElementById('cyclorama');
  var renderer = new _renderer.Renderer(root);

  renderer.initialize(cyclorama);

  // :: --- initialize vue bindings

  (0, _vue.initializeApp)({
    rotors: [root],
    renderer: renderer
  });
});
